/* -----------------------------------------Main Content------------------------- */


  #main {
    
    margin: 16px auto;
    width: auto;
    height: auto;
    
    display: flex;
    justify-content: space-evenly;
  }

  #main_title{
    word-wrap: break-word;
    font-family: 'Poppins', sans-serif;
    margin: auto;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: space-evenly;
    white-space:normal;
  }

  #main_title span{
      margin-top: 10px;
  }

  #main img{
    width: 120px;
    /* height: 140px; */
}


.it1{
    margin-left: -22px;
}

.it2{
     margin-left: -50px;
}

.it3{
    margin-right: 50px;
}


  

/* ===============================Main Content========================= */

.out-cover{
    width: 100%; height: 120vh;
    /* position: absolute; */
    /* width: 1314px; */
    /* height: 648px; */
    /* background-color: rgba(247, 223, 162, 0.15); */
    text-align: center;
    overflow-x: hidden !important;
    margin-top: 120px;
    font-family: 'Poppins', sans-serif;

}

.out-cover a{
    
    text-decoration: none;
    
}

.home-content{
    background-image: url(/react-figma-task/static/media/download.ce026387.png);
    width: 100% !important;
    height: 120vh;
    /* height: 100%; */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}



.home-heading{
    font-family: 'Raleway', sans-serif !important;
    font-weight: 500;
    font-size: 74px !important;
    
}


.home-paragraph{
    
    font-weight: 400;
     
    height: 423px;
    font-size: 28px;
    line-height: 48px;
    letter-spacing: 0.42px;
    margin: 50px auto 0px auto;
    width: 65%;
    
}


/* -------------------------------Media Query------------------------- */
@media only screen and (max-width: 1340px) {
    .home-content{
        height: 80vh;
    }

}

@media only screen and (max-width: 1140px) {
    .home-paragraph{
        width: 80%;
    }

    #main img{
        width: 100px;
    }

    #main_title{
        font-size: 12px;
        
    }

}


@media only screen and (max-width: 920px) {

    #main img{
        width: 80px;
    }
    .home-content{
        height: 50vh;
    }

    .home-paragraph{
        width: 95%;
    }

    .it1{
        margin-left: 0;
    }
    
    .it2{
         margin-left: 0;
    }
    
    .it3{
        margin-right: 0;
    }

    #main_title{
        font-size: 8px;
        width: 95%;
        
    }

    .content p{
        font-size: 28px !important;
        }
    
    .content{
        width: 95% !important;
    }
    
    .custombtn2{
        margin-right: 0px !important;
    }

    .bar button{
        margin-left: 0px !important;


}


}


@media only screen and (max-width: 600px){
    #main img{
        width: 80px;
    }
    
}




